import Cover from './CoverComponent'
import Footer from './FooterComponent';
import { Card, CardBody } from 'reactstrap';
import Fade from 'react-reveal/Fade';

function RenderCard(params) {
  let order1, order2;
  if(params.imageLeft){
    order1 = "order-md-1";
    order2 = "order-md-2";
  } else {
    order1 = "order-md-2";
    order2 = "order-md-1";
  }
  return (
    <Card id="features">
      <CardBody>
        <div className="row">
          <div className={"col-md-4 d-flex justify-content-center " + order1}>
            <img className="py-4" src={params.image} alt="Cortech" />
          </div>
          <div className={"col-md-8 " + order2}>
            <h2>{params.title}</h2>
            <p>{params.description}</p>
          </div>

        </div>
      </CardBody>
    </Card>);

}

function Home() {
  return (
    <div>
      <Cover />
      <div className="container">
        <br></br>
        <Fade left>
          <div className="row">
            <div className="col-md-10 offset-md-1">
              <blockquote className="blockquote">
                <p className="mb-0 quote font-italic">"Logic will get you from A to B, imagination will take you everywhere."</p>
                <footer className="blockquote-footer">Albert Einstein</footer>
              </blockquote>
            </div>
          </div>
        </Fade>

        <br></br>
        <Fade right>
          <RenderCard title="Why choose CorTech?" image="assets/images/why.png" imageLeft={true} description="Would you like better rankings in search engines and improve your revenue? Do you require a CRM tool that accommodates to your business? Would you like an integrated software platform to measure your results? Cortech offers a variety of packages with dynamic tools that can be easily adapted to your needs. We work with all kinds of companies and we guarantee fast and easy results." />
        </Fade>
        <br></br>
        <Fade left>
          <RenderCard title="Our team" image="assets/images/our-team.png" imageLeft={false} description="Cortech specializes in Internet Marketing and IT solutions. We have personnel specialized in marketing, SEO, graphic design, web design, IT, software development and sales, programming and more! With a variety of creative members dedicated and passionate and goal oriented. You are in good hands with this group of accomplished professionals." />
        </Fade>
        <br></br>
        <Fade right>
          <RenderCard title="Action" image="assets/images/action.png" imageLeft={true} description="We take pride on ensuring that we attend every industry related conference, online presentation and keep up with the content to stay ahead of the latest trends in this ever changing industry." />
        </Fade>

        <br></br>
      </div>
      <Footer />
    </div>
  );
}

export default Home;